const UPDATE_NEW_MESSAGE_BODY = 'UPDATE_NEW_MESSAGE_BODY';
const SEND_MESSAGE = 'SEND_MESSAGE';

let initialState = {
    dialogs: [
        {id:1, name: 'Dima'},
        {id:2, name: 'Anton'},
        {id:3, name: 'Lera'},
        {id:4, name: 'Vitala'},
        {id:5, name: 'Sveta'},
        ],
  
      messages: [
        {id:1, message: 'HI'},
        {id:2, message: '–ö–∞–∫ –æ–Ω–æ?'},
        {id:3, message: '–ö–æ–≥–¥–∞  —Å–¥–∞—à—å –ø—Ä–æ–µ–∫—Ç? –ü—Ä–∏–≤–µ—Ç, –∫–∞–∫ –¥–µ–ª–∞? Lorem ipsum, dolor sit amet consectetur adipisicing elit. Error, deleniti qui molestiae reprehenderit, optio cumque consequatur blanditiis reiciendis molestias tenetur magnam totam delectus laborum similique recusandae. Corrupti assumenda temporibus reprehenderit!'},
      ],
};

const dialogsReducer = (state = initialState, action) => {
    
    switch (action.type) {
        case UPDATE_NEW_MESSAGE_BODY:
            return {
                ...state,
                newMessageBody: action.body, //–º–µ–Ω—è–µ–º —Å—Ç–µ–π—Ç, —Å–æ—Ö—Ä–∞–Ω—è–µ–º —Ç–µ–∫—Å—Ç –∏–∑ –ø–æ–ª—è –≤–≤–æ–¥–∞
            };
        case SEND_MESSAGE:
            let body = action.newMessageBody; //–∫–æ–ø–∏—Ä—É–µ–º —Å–æ—Ö—Ä–∞–Ω—ë–Ω–Ω—ã–π —Ç–µ–∫—Å—Ç, –≤–æ –≤—Ä–µ–º–µ–Ω–Ω—É—é –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é
            return {
                ...state,
                messages: [...state.messages, { id: 6, message: body }] //–æ—Ç–ø—Ä–∞–≤–ª—è–µ–º —Ç–µ–∫—Å—Ç –≤ –±–∞–∑—É
            }; //—Ç–∏–ø–æ –≤ –Ω–∞—á–∞–ª–µ –ø–µ—Ä–µ–¥–∞—ë–º –≤—Å–µ –∏–∑–≤–µ—Å—Ç–Ω—ã–µ –ø–µ—Ä–µ–º–µ–Ω–Ω—ã–µ, –∞ –ø–æ—Å–ª–µ–¥–Ω–µ–π –Ω–∞–∑–Ω–∞—á–∞–µ–º —Ç–æ —á—Ç–æ –Ω–∞–º –Ω—É–∂–Ω–æ

        default:
            return state;
    }
}

export const sendMessageCreator = (newMessageBody) => ({type: SEND_MESSAGE, newMessageBody})

export default dialogsReducer;