import {connect} from 'react-redux'
import { follow, unfollow, setCurrentPage,  reqestUsers } from '../../redux/usersReduces'
import React from 'react'
import Users from './Users'
import Preloader from '../common/Preloader/Preloader'
import { withAuthRedirect } from '../../HOC/withAuthRedirect'
import { compose } from 'redux'
import {getCurrentPage, getFollowingInProgress, getIsFetching, getPageSize, getTotalUsersCount, getUsers } from '../../redux/usersSelectors'

class UsersContainer extends React.Component { //–Ω–∞—Å–ª–µ–¥—É—é —Ä–µ–∞–∫—Ç –∫–æ–º–ø–∞–Ω–µ–Ω—Ç—É

    componentDidMount() {   //–∞—è–∫—Å –∑–∞–ø—Ä–æ—Å
                            //–≤—ã–∑—ã–≤–∞–µ—Ç—Å—è –æ–¥–∏–Ω —Ä–∞–∑ –ø—Ä–∏ –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —ç—Ç–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã, –ø–æ—Å–ª–µ —Ä–µ–Ω–¥–µ—Ä–∞          
        let {currentPage, pageSize} = this.props;
        this.props.reqestUsers(currentPage, pageSize);
    }

    onPageChanged = (pageNumber) => {
        this.props.reqestUsers(pageNumber, this.props.pageSize);
    }

    render() {     
        return <>
                {this.props.isFetching ? <Preloader/>: null}
        <Users totalUsersCount={this.props.totalUsersCount}
                        pageSize = {this.props.pageSize}
                        currentPage = {this.props.currentPage}
                        onPageChanged = {this.onPageChanged} //—ç–∫—Å–ø–æ—Ä—Ç —Ñ—É–Ω–∫—Ü–∏–∏ –ø—Ä–æ–≤–æ–¥–∏—Ç—Å—è –±–µ–∑ –ø—Ä–æ–ø—Å–∞
                        users={this.props.users}
                        follow = {this.props.follow}
                        unfollow = {this.props.unfollow}
                        followingInProgress={this.props.followingInProgress}
                        ></Users>
        </>
    }
}

let mapStateToProps = (state) => {
    return {
        users: getUsers(state), //–≤ –∫–æ–º–ø–∞–Ω–µ–Ω—Ç—É –ø—Ä–∏–¥—É—Ç —é–∑–µ—Ä—ã, –∑–Ω–∞—á–µ–Ω–∏—è–º–∏ –∫–æ—Ç–æ—Ä–æ–≥–æ –±—É–¥—É—Ç —é–∑–µ—Ä—ã –∏–∑ —Å—Ç–µ–π—Ç–∞
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
}

export default compose (
    withAuthRedirect,
    connect(mapStateToProps, 
        {follow, unfollow,  //—Ñ—É–Ω–∫—Ü–∏—è connect —Å–∞–º–∞ –º–æ–∂–µ—Ç –¥–∏—Å–ø–∞—Ç—á–∏—Ç—å action creator
        setCurrentPage, //follow:follow (–ø–æ–ª–Ω–æ—Å—Ç—å—é –ø–∏—Å–∞—Ç—å –Ω–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ)
        reqestUsers})
)(UsersContainer)

