import { applyMiddleware, combineReducers, createStore, compose } from "redux";
import authReducer from "./authReducer";
import dialogsReducer from './dialogsReducer';
import profileReducer from './profileReducer';
import sidebarReducer from './sidebarReducer';
import usersReducer from "./usersReduces";
import thunkMiddleware from 'redux-thunk'
import { reducer } from "redux-form";
import appReducer from "./appReducer";

let reducers = combineReducers(  //—ç—Ç–æ —á—Ç–æ-—Ç–æ —Ç–∏–ø–æ —Å—Ç–µ–π—Ç–∞
    {
        dialogsPage: dialogsReducer, //–º—ã —Å–æ–∑–¥–∞—ë–º –æ–±—ä–µ–∫—Ç
        profilePage: profileReducer, // —Å–æ–≤–æ–π—Å—Ç–≤–æ–º:–∑–Ω–∞—á–µ–Ω–∏–µ–º
        sidebarPage: sidebarReducer,
        usersPage: usersReducer,
        auth: authReducer,
        form: reducer,
        app: appReducer,
    }
)

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, composeEnhancers(  //—Å–∞–º —Å–æ–∑–¥–∞—ë—Ç —Å—Ç–µ–π—Ç —Å–æ —Å–≤–æ–π—Å—Ç–≤–∞–º–∏
        applyMiddleware(thunkMiddleware)    // –≤–∫–ª—é—á–∞–µ—Ç –ø—Ä–æ–º–µ–∂—É—Ç–æ—á–Ω—ã–π —Å–ª–æ–π
    ));
    
export default store;
