import './App.css';
import Navbar from './components/NavBar/NavBar';
import { BrowserRouter, Route, withRouter } from 'react-router-dom'
import UsersContainer from './components/Users/UsersContainer';
import HeaderContainer from './components/Header/HeaderContainer\'';
import Login from './components/Login/Login';
import { connect } from 'react-redux';
import { initializeApp } from './redux/appReducer';
import { compose } from 'redux';
import Preloader from './components/common/Preloader/Preloader';
import store from './redux/redux-store';
import { Provider } from 'react-redux';
import { Suspense, useEffect } from 'react';
import React from 'react';
import { withSuspense } from './HOC/withSuspense';

const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'))
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'))

//–∏–º–ø–æ—Ä—Ç –ø–æ –º–µ—Ä–µ –Ω–∞–¥–æ–±–Ω–æ—Å—Ç–∏

const App = (props) => {

  useEffect(() => { // –≤—ã–ø–æ–ª–Ω–∏—Ç —Ñ—É–Ω–∫—Ü–∏—é –ø–æ—Å–ª–µ –æ—Ç—Ä–∏—Å–æ–≤–∫–∏
    props.initializeApp();
  }) // –≤—ã–ø–æ–ª–Ω—è–µ—Ç—Å—è –≤ –º–æ–º–µ–Ω—Ç –∏–∑–º–µ–Ω–µ–Ω–∏—è [—ç—Ç–æ–π] –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π
  // componentDidMount() {
  //   this.props.initializeApp();
  // }

  if (!props.initialized) {
    return <Preloader />
  }
  return (
    <div className="app-wrapper">
      <HeaderContainer></HeaderContainer>
      <Navbar></Navbar>
      <div className='app-wrapper-content'>
        <Route path='/dialogs' render={withSuspense (DialogsContainer)} />
        <Route path='/profile/:userId?' render={withSuspense (ProfileContainer)}/> {/*:userId - —ç—Ç–æ –æ–±—ä—è–≤–µ–ª–Ω–∏–µ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞, –¥–ª—è withRoute, –≤–æ–ø—Ä–æ—Å –æ–±–æ–∑–Ω–∞—á–∞–µ—Ç –Ω–µ—Å—Ç–æ–≥–∏–π –ø–∞—Ä–∞–º–µ—Ç—Ä*/}
        <Route path='/users' render={() => <UsersContainer />}></Route>
        <Route path='/login' render={() => <Login />}></Route>
      </div>
    </div>
  );
}


const mapStateToProps = (state) => ({
  initialized: state.app.initialized,
})

const AppContainer = compose(
  withRouter,
  connect(mapStateToProps, { initializeApp }))(App)

export const MainApp = (props) => {
  return <BrowserRouter>   {/* BrowserRouter ‚Äî —Å–ª–µ–¥—É–µ—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –∫–æ–≥–¥–∞ –≤—ã –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç–µ –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ –¥–∏–Ω–∞–º–∏—á–µ—Å–∫–∏–µ –∑–∞–ø—Ä–æ—Å—ã, –∞ HashRouter –∏—Å–ø–æ–ª—å–∑—É–π—Ç–µ –∫–æ–≥–¥–∞ —É –≤–∞—Å —Å—Ç–∞—Ç–∏—á–µ—Å–∫–∏–π –≤–µ–± —Å–∞–π—Ç. */}
    <Provider store={store}  >
      <AppContainer />
    </Provider>
  </BrowserRouter>
}