import React from 'react'
import { connect } from 'react-redux'
import { Field, reduxForm } from 'redux-form'
import { required } from '../../utils/validators/validators'
import { Input } from '../common/FormsControls/FormsControls'
import { login } from '../../redux/authReducer'
import { Redirect } from 'react-router-dom'
import style from '../common/FormsControls/FormsControls.module.css'

const LoginForm = ({handleSubmit, error}) => {
    return <form onSubmit={handleSubmit}> {/*–ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ —Ñ–æ—Ä–º—É –∑–∞–ø—É—Å–∫–∞–µ—Ç —Ñ—É–Ω–∫—Ü–∏—é –æ–±—Ä–∞–±–æ—Ç—á–∏–∫*/}
    <div>
        <Field placeholder = {'Email'} 
                name = {'email'}   
                component = {Input}
                validate={[required]}></Field>
    </div>
    <div>
        <Field placeholder = {'Password'} 
                name = {'password'} 
                type = {'password'} 
                component = {Input}
                validate={[required]}></Field>
    </div>
    <div>
        <Field type = {'checkbox'} name = {'rememberMe'} component = {Input}/>remember me
    </div>
    {error && <div className = {style.formSummaryError}>
        {error}
    </div>}
    <div>
        <button>Login</button> {/*–ø–æ–∫–∞ –∫–Ω–æ–ø–∫–∞ –≤ —Ñ–æ—Ä–º–µ, –æ–Ω–∞ –ø–æ –¥–µ—Ñ–æ–ª—Ç—É –ø–µ—Ä–µ–∑–∞–≥—Ä—É–∂–∞–µ—Ç —Å—Ç—Ä–∞–Ω–∏—Ü—É*/}
    </div>
</form>
}

const LoginReduxForm = reduxForm 
                    ({form: 'login'}) //–Ω–∞–∑–≤–∞–Ω–∏–µ
                    (LoginForm) // –∫–æ–º–ø–∞–Ω–µ–Ω—Ç–∞

const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememderMe)
    }// –∑–¥–µ—Å—å –ª–æ–≥–∏–Ω —ç—Ç–æ –∫–æ–ª–±–µ–∫ —Ñ—É–Ω—Ü–∏—è, –∫–æ—Ç–æ—Ä–∞—è –¥–∏—Å–ø–∞—Ç—á–∏—Ç –≤—ã–∑–æ–≤ —Å–∞–Ω–∫–∏ (—Ç–∞ —á—Ç–æ –≤ –∫–æ–Ω–Ω–µ–Ω–∫—Ç–µ)
    if (props.isAuth) {
        return <Redirect to = {'/profile'}/>
    }
    return <div>
        <h1>Login</h1>
        <LoginReduxForm onSubmit = {onSubmit}/>
    </div>
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect (mapStateToProps, {login}) (Login);